aside.sidebar {
  position: fixed;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 2; }

.fragment:target {
  padding-top: 45px; }

[class*=scroll-fire-box] {
  min-height: 640px; }

.scroll-fire-box-1,
.scroll-fire-box-3 {
  background-color: #4c5866; }

.scroll-fire-box-2 {
  background-color: #b22222;
  transition: background-color 1s ease-out; }
  .scroll-fire-box-2 > i.moss-icon {
    font-size: 12em;
    color: #fff;
    transition: all 1s ease-out; }
  .scroll-fire-box-2.toggled {
    background-color: #f2f2f2; }
    .scroll-fire-box-2.toggled > i.moss-icon {
      font-size: 16em;
      color: #b22222; }
